.advantageContainer{
position: relative;
}

.advantageRow{
    display:flex;
    padding: 5rem;
}

.left{
    flex:1;
    text-align: left;
    position: relative;
}

.left h5{
    font-family: Inter;
    font-weight: 700;
    font-size: 1rem;
    text-align: left;
    color: var(--primary);
}

.left h3{
    font-family: Poppins;
    font-weight: 700;
    font-size: 1.5rem;
    color: #464646;
    margin-top: 1rem;
    text-align: left;
}
.titlebreak{
    transform: scale(0.3);
    margin-left: -9rem;

    
}

.left h4{
    font-family: Inter;
    font-weight: 700;
    font-size: 1rem;
    text-align: left;
    color: #9d9d9d;
    margin-top: 1rem;
}

.right{
    flex:1;
}

.right>img{
    height:500px;
    width: 592px;
}

.cardContainer{
    display: flex;
    position: absolute;
    top: 16rem;
    left: 5rem;
    gap: 3.5rem;
    z-index: 4;

}

