.leftBox{
  display: grid;
  grid-template-columns: 0.3fr 0.4fr 0.3fr;
  
}
  .column1 {
    padding: 10px;
  }
  .column2{
    margin-right: 20px;
    align-self: center;
  }
  .column1__highlight{
    line-height: 30px;
    padding-right: 10px;
    color: #4E4E4E;
  }
  .column1__heading{
    color: var(--primary);
    padding-top: 10px !important;
  }
  .column2__heading{
    text-align: center;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 700;
    line-height: 42px;
    font-size: 32px;
    text-align: center;
    color: #464646;

  }
  .column2__upperText{
    color: var(--primary);
    font-size: 1.2rem ;
    line-height: 30px ;
    font-weight: 600 ;
    text-align: center;

  }
.column2 img {
    /* height: 50vh; */
    width: 100%;
}
