.OurServiceBox{
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.88) 0%, rgba(5, 91, 87, 0.88) 125.98%, rgba(8, 140, 135, 0.88) 195.07%)
}
.ServiceContent{
    display: grid;
    grid-template-columns: 0.55fr 0.45fr;
    gap:20px;
}
.BigBoxLayout{
    max-width: 260px !important;
    min-height: 298px;
    border-radius: 8px !important;
}
.AlignBoxes{
    padding: 40px 0;
    display: flex;
    flex-direction: column;
    row-gap: 30px; 

}
.AlignBoxesTwo{
    padding: 70px 0;
    display: flex;
    flex-direction: column;
    row-gap: 30px; 

}
.iconLayout{
    width: 50px;
    padding-bottom: 0px !important;
}
.ourServiceRight{
    padding: 90px 0 0 30px;

}
.ourServiceRight--mainText{
    text-transform: uppercase;
    color: var(--primary);
    letter-spacing: 0.08em;
}
.ourServiceRight--heading{
    font-family: 'Poppins';
font-style: normal;
font-weight: 700;
color: #FFFFFF;
line-height: 42px;
padding-right: 20px;

}
.ourServiceRight--text{
    line-height: 38px;
    color: rgba(255, 255, 255, 0.7);
    font-weight: 500;
}