@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');

.header{
    display: grid;
    position: fixed;
    width: 100%;
    grid-template-columns: minmax(100px, 250px) minmax(200px, 1fr) minmax(200px, 300px);
    align-items: center;
    height: 60px;
    z-index: 4;
      
}

.left img{
width: 100%;
height: 46px;

}

.right{
    display: flex;
    align-items: center;
    justify-content: center;
}

.middle ul{
    display: flex;
    /* font-family: Inter; */
    color: white;
    column-gap: 2rem;
    flex-wrap: wrap;
    justify-content: center;
    font-weight: bold
    
}

.header-menu>ul>li:hover{
 cursor:pointer;
 color: var(--primary);
}

.header-btn{
    padding: 10px 40px;
    width: 190px;
    height:45px;
    font-family: Inter;
    background-color: var(--primary);
    color: white;
    border-radius: 4px;
    border: none;
    font-weight: bold;

}

