.intro{
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;


    background-image: linear-gradient(90.3deg, rgba(0, 0, 0, 0.3) 0.24%, rgba(67, 174, 170, 0.237) 98.28%), linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.6) 100%), url(../../assets/shutterstock_1.jpg);
}
.introBox{
    position: relative;
    top: 55px;
    height: 90%;
    flex-direction: column;
    text-align: center;
    width: 100%;

}
.introBox h1 {
    font-size: 46px;
    /* text-align:start */
}

.introBox h3 {
    /* text-align:start; */
    font-weight:500;
    opacity: 0.6;
    margin-left: 5px;
}
