.card{
    height: 183px;
    width: 181px;
    background-color: white;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
    padding: 3rem;
}

.card h5{
    margin-top:0.5rem;
    color: var(--primary);
    font-family: Inter;
    font-weight: 700;
    font-size: 1.5rem;

}

.card h6{
    color: #4e4e4e;
    font-family: Inter;
    font-weight: 700;
    margin-top: 1rem;
    font-size: 1rem;

}

.card p {
    color: #9e9ea0;
    font-family: Inter;
    font-weight: 400;
    font-size: 0.8rem;
    margin-top: 1rem;
}