.WhyUsBox{
  display: grid;
  grid-template-columns: 0.4fr 0.6fr;

}
.textBox{
    background-color: white;
    border-radius: 6px;
}
.textBox__para{
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 30px;
color: #848589;
}
.textBox__name{
  padding: 20px 0 0 0 ;
  font-family: 'Poppins';
font-weight: 600;
font-size: 20px;
color: #2D2D2D;
}
.textBox__designation{
  padding: 10px 0 0 0;
font-weight: 500;
color: #9E9EA0;

}
